import React, { Component } from 'react';
import './App.css';
import {BrowserRouter as Router, Link, Route} from 'react-router-dom';
import Home from './Components/Home';

class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <div className="header">
            <ul id="header-menu">
              <Link to="/football/"><li>Home</li></Link>
              <Link to="/football/scores"><li>Scores</li></Link>
              <Link to="/football/draft"><li>Draft</li></Link>
            </ul> 
          </div>
          <div className="App">
            <Route exact path="/football/" component={Home} />
            <Route path="/football/scores" component={Scores} />
            <Route path="/football/draft" component={Draft} />
          </div>
        </div>
      </Router>
    );
  }
}

const Scores = () => (
  <div>
    <h1>Scores</h1>
    <p>Scoreboard will go here</p>
  </div>
);

const Draft = () => (
  <div>
    <h1>Draft</h1>
    <p>Draft results will be displayed here</p>
  </div>
);

export default App;
