import React, { Component } from 'react';
import './StandingsCard.css';

class StandingsCard extends Component {
  render() {
    const {title, records} = this.props;
    const standings = records.map((line, index) => (
      <li key={title+index}>
        <span className="name">{line.name}</span>
        <span className="record">{line.wins}</span>
        <span className="record">{line.losses}</span>
        <span className="record">{line.ties}</span>
      </li>
    ));
    return (
      <div className="standingsCard">
        <h1>{title}</h1>
        <ol>{standings}</ol>
      </div>
    );
  }
}

export default StandingsCard;